import OpenAI from 'openai';
import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';

function App() {
  const [chat, setChat] = useState([]);
  const openai = new OpenAI('sk-proj-UaxNrpxftESIEJmhi56hT3BlbkFJgtZOMqegr7MGOVWLvibx');

  const generateResponse = async (input) => {
    const gptResponse = await openai.complete({
      engine: 'text-davinci-002',
      prompt: input,
      maxTokens: 60,
    });

    return gptResponse.data.choices[0].text.trim();
  };

  const handleUserInput = async (event) => {
    if (event.key === 'Enter') {
      const userInput = event.target.value;
      setChat([...chat, { user: 'User', text: userInput }]);
      event.target.value = '';

      let aiResponse = await generateResponse(userInput);
      if (!aiResponse) {
        aiResponse = 'I am sorry, I did not understand that.';
      }

      const storedResponses = JSON.parse(localStorage.getItem('responses')) || {};
      storedResponses[userInput] = aiResponse;
      localStorage.setItem('responses', JSON.stringify(storedResponses));

      setChat([...chat, { user: 'AI', text: aiResponse }]);
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <div>
          {chat.map((message, index) => (
            <p key={index}><strong>{message.user}:</strong> {message.text}</p>
          ))}
        </div>
        <input type="text" onKeyDown={handleUserInput} />
      </header>
    </div>
  );
}

export default App;